<template>
	<div id="guessingCompetition">
		<div class="camp left">
			<gCCamp :campT="'red'"></gCCamp>
		</div>
		<div class="camp right">
			<gCCamp :campT="'blue'"></gCCamp>
		</div>
	</div>
</template>

<script>
	import gCCamp from '@/components/GCCamp';
	import gCCampClass from '@/lib/GCCampClass';
	
	export default{
		data(){
			return{
				gCCampClass
			}
		},
		beforeCreate() {
			if(!sessionStorage.sessionRedCamp){
				let gCCampObject = new gCCampClass;
				sessionStorage.sessionRedCamp = gCCampObject.getJson();
			}
			if(!sessionStorage.sessionBlueCamp){
				let gCCampObject = new gCCampClass;
				sessionStorage.sessionBlueCamp = gCCampObject.getJson();
			}
		},
		computed:{
		},
		components:{
			gCCamp
		},
		mounted(){
		}
	};
</script>

<style>
	#guessingCompetition{
		width: 90vw;
		height: 45vw;
		margin: auto;
		background-color: #523f39;
		display: flex;
	}
	#guessingCompetition .camp{
		flex:1;
		height: 100%;
	}
	#guessingCompetition .left{
		background-color: #8b332f;
	}
	#guessingCompetition .right{
		background-color: #587593;
	}
	
	.gCCamp{
		margin-top: 1.5vw !important;
	}
</style>



