<template>
	<div class="gCCamp" @click="showAttribute">
		<gCShikigami v-for="(item,index) in campList" :shikigamiT="index" :campT="camp"></gCShikigami>
		<gCCampAttribute v-if="isShow" @close="closeAttribute" :campT="camp"></gCCampAttribute>
	</div>
</template>

<script>
	import gCShikigami from './GCShikigami';
	import gCCampAttribute from './GCCampAttribute';
	
	export default{
		props:[
			'campT'
		],
		data(){
			return{
				camp : this.campT,
				isShow : false
			}
		},
		components:{
			gCShikigami,
			gCCampAttribute
		},
		methods:{
			showAttribute(){
				this.isShow = true;
			},
			closeAttribute(){
				this.isShow = false;
			}
		},
		computed:{
			campList(){
				return this.$store.getters.getCamp(this.camp);
			}
		}
	};
</script>

<style>
	.gCCamp{
		width: 40.5vw;
		height: 13.5vw;
		margin: auto;
		outline: #cfa5b1 2px solid;
		display: flex;
	}
</style>



