import ShikigamiClass from './ShikigamiClass.js';

class GCCampClass{
	constructor() {
	    this.shikigamiList = new Array(new ShikigamiClass,new ShikigamiClass,new ShikigamiClass,new ShikigamiClass,new ShikigamiClass);
	}
	
	getJson(){
		return  JSON.stringify(this);
	}
	
	static getObject(str){
		return JSON.parse(str);
	}
}

export default GCCampClass;


