<template>
	<div id="GCCampAttribute" @click="">
		<div class="table">
			<div class="tr">
				<div class="td">
					红方
				</div>
				<div class="td">
					攻击
				</div>
				<div class="td">
					生命
				</div>
				<div class="td">
					防御
				</div>
				<div class="td">
					速度
				</div>
				<div class="td">
					暴击
				</div>
				<div class="td">
					暴击伤害
				</div>
				<div class="td">
					效果命中
				</div>
				<div class="td">
					效果抵抗
				</div>
				<div class="td">
					御魂效果
				</div>
			</div>
			<div class="tr" v-for="(item,index) in campList">
				<div class="td">
					<img src="" @click="openShikigamiAttribute(index)"/>
					<span>{{item.shikigamiName}}</span>
				</div>
				<div class="td">
					{{item.gCAttack}}
				</div>
				<div class="td">
					{{item.gCLife}}
				</div>
				<div class="td">
					{{item.gCDefense}}
				</div>
				<div class="td">
					{{item.gCSpeed}}
				</div>
				<div class="td">
					{{item.gCCrit}}
				</div>
				<div class="td">
					{{item.gCCriticalDamage}}
				</div>
				<div class="td">
					{{item.gCEffectHit}}
				</div>
				<div class="td">
					{{item.gCEffectResistance}}
				</div>
				<div class="td">
				</div>
			</div>
		</div>
		<div class="closeCampAttributeButton" @click="closeAttribute">
			X
		</div>
		<shikigamiAttribute :state="'add'" v-if="isShow" @close="closeShikigamiAttribute" :camp="campT"></shikigamiAttribute>
	</div>
</template>

<script>
	import shikigamiAttribute from './ShikigamiAttribute';
	
	export default{
		props:[
			'campT'
		],
		data(){
			return{
				isShow : false,
				shikigamiIndex : null,
				shikigami : null
			}
		},
		methods:{
			//关闭队伍属性
			closeAttribute(){
				this.$emit('close');
				event.stopPropagation();
			},
			//打开对应式神属性设置
			openShikigamiAttribute(index){
				this.isShow = true;
				this.shikigamiIndex = index;
				this.shikigami = this.$store.getters.getGCShikigami(this.campT,index);
			},
			//关闭式神属性设置
			closeShikigamiAttribute(){
				this.isShow = false;
			}
		},
		computed:{
			campList(){
				return this.$store.getters.getCamp(this.campT);
			}
		},
		components:{
			shikigamiAttribute
		}
	};
</script>

<style>
	#GCCampAttribute{
		position: fixed;
		width: 94vw;
		height: 47vw;
		background-color: #8b6851;
		left: 50%;
		margin-left: -47vw;
		top:50%;
		margin-top: -23.5vw;
		display: flex;
	}
	#GCCampAttribute .closeCampAttributeButton{
		width: 5.897vw;
		height: 5.897vw;
		line-height: 5.897vw;
		text-align: center;
		font-size: 3vw;
		border-radius: 50%;
		background: #783a45;
		right: -2vw;
		top:1vw;
		position: absolute;
	}
	#GCCampAttribute .table{
		background-color: #c7b59e;
		margin: auto;
		width: 97%;
		height: 94%;
		display: flex;
	}
	#GCCampAttribute .table .tr{
		display: flex;
		flex-direction:column;
		flex: 1;
	}
	#GCCampAttribute .table .tr:first-of-type{
		flex: initial;
		width:12.8%;
	}
	#GCCampAttribute .table .tr .td{
		flex:1;
		display: flex;
		align-content:center;
		justify-content:center;
		align-items:center;
		font-size: 1.5vw;
		flex-direction:column;
	}
	#GCCampAttribute .table .tr .td img{
		background-color: #CFA5B1;
		width: 5.5vw;
		height: 5.5vw;
	}
	#GCCampAttribute .table .tr .td:first-of-type{
		height: 19.7%;
		flex: initial;
		font-size: 1.7vw;
	}
	#GCCampAttribute .table .tr .td:last-of-type{
		height: 13.3%;
		flex: initial;
	}
</style>



