import Vue from 'vue';
import Vuex from 'vuex';
import gCCampClass from '@/lib/GCCampClass';

Vue.use(Vuex)

let store = new Vuex.Store({
	state:{
		gCCampClass
	},
	getters:{
		//根据参数获取队伍数据
		getCamp:(state,getters) => (camp) =>{
			if(camp == "red"){
				let redCampStringList = gCCampClass.getObject(sessionStorage.sessionRedCamp);
				return redCampStringList.shikigamiList;
			}
			if(camp == "blue"){
				let blueCampStringList = gCCampClass.getObject(sessionStorage.sessionBlueCamp);
				return blueCampStringList.shikigamiList;
			}
			return null;
		},
		//根据所在队伍和位置获取式神
		getGCShikigami:(state,getters) => (camp,index) =>{
			return getters.getCamp(camp)[index];
		}
	},
	mutations:{
		
	}
})

export default store


