import Vue from 'vue'
import Router from 'vue-router'
import '@/assets/css/default.css'

Vue.use(Router)

import GuessingCompetition from '@/views/GuessingCompetition'

export default new Router({
  routes: [
    {
      path: '/',
      name: 'GuessingCompetition',
      component: GuessingCompetition
    }
  ]
})



